body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

nav {
  background-color: #282d3b !important;
}

@media screen and (max-width: 600px) {
  .pt1 {
    padding-top: -0.75rem !important;
  }
}
.nav-list li {
  font-size: 130%;
}
.nav-list li a {
  font-size: 100%;
}
.border {
  border: 1px solid black;
}
.starColor {
  color: #ffbe00;
}
.font {
  font-family: "Rubik", sans-serif !important;
}
.numOrders {
  display: inline-block;
  background-color: #2a9cf5;
  color: #fff;
  padding: 8px 20px;
  border-radius: 6px;
  margin-left: 20px;
  position: relative;
}
.blueColor {
  color: #2a9cf5;
}

#bg {
  background-image: url("http://idealbrothers.thesoftking.com/html/bigenza/bigenja/assets/img/bg/header-bg.jpg");
}
.poppins {
  font-family: "Poppins", sans-serif;
}
#ladies-wear {
  background-image: url("http://idealbrothers.thesoftking.com/html/bigenza/bigenja/assets/img/bg/ladies-cloths-bg.jpg");
}
#shipping {
  background-image: url("http://idealbrothers.thesoftking.com/html/bigenza/bigenja/assets/img/bg/shipping-bg.jpg");
}

.boldFont {
  color: #232b37;
  font-weight: 600;
  font-family: "Rubik", sans-serif;
}
.rubikFont {
  font-family: "Rubik", sans-serif;
}
.addToCart li {
  display: inline-block;
}
.qty-minus {
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  display: inline-block;
  width: 40px;
  height: 40px;
  border: 2px solid #e5e5e5;
  text-align: center;
  line-height: 40px;
  cursor: pointer;
  font-weight: 500;
}
.qty-total {
  display: inline-block;
  width: 40px;
  height: 40px;
  border: 2px solid #e5e5e5;
  text-align: center;
  line-height: 40px;
  cursor: pointer;
  font-weight: 500;
}
.qty-plus {
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  display: inline-block;
  width: 40px;
  height: 40px;
  border: 2px solid #e5e5e5;
  text-align: center;
  line-height: 40px;
  cursor: pointer;
  font-weight: 500;
}
.activities li {
  display: inline-block;
  padding-right: 2%;
}
.activitiesIcon {
  display: inline-block;
  width: 40px;
  height: 40px;
  background-color: #eeeeee;
  border: 2px solid #eeeeee;
  color: #838b97;
  border-radius: 5px;
  text-align: center;
  line-height: 40px;
  cursor: pointer;
  font-weight: 500;
}
.activitiesIcon:hover {
  background-color: rgb(42, 156, 245);
  color: white;
}

.input-field {
  border: 2px solid #efefef !important;
  width: 100%;
  height: 60px;
  padding: 0 30px;
  border-radius: 6px;
  color: red;
}
.submit-button {
  color: #fff;
  text-transform: uppercase;
  background-color: #2a9cf5;
  border: none;
}
.coupon::-webkit-input-placeholder {
  color: "red";
}
.coupon::-ms-input-placeholder {
  color: "red";
}
.coupon::placeholder {
  color: "red";
}
#couponInput {
  border: 1px solid black;
  border-radius: 5px;
}
#couponInput::-webkit-input-placeholder {
  color: rgb(73, 72, 72);
  padding-left: 8%;
}
#couponInput::-ms-input-placeholder {
  color: rgb(73, 72, 72);
  padding-left: 8%;
}
#couponInput::placeholder {
  color: rgb(73, 72, 72);
  padding-left: 8%;
}

